.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.banner {
  background-color: #fff1a6;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  display: flex;
  flex-wrap: wrap;
  /* justify-content: stretch; */
  width: 100%;
  vertical-align: baseline;
  background-color: #fff1a6;
}

.item h1, .item h2, .item h3 {
  margin: 0;
  text-align: center;
}

.item {
  display: flex;
  width: 100%;
  min-height: 350px;
  flex-wrap: wrap;
  margin-bottom: 1%;
  justify-content: center;
  align-items: center;
}

/* .item:nth-child(3n-2) {
  width: 100%;
} */

.weather-alert-container {
  display: flex;
  justify-content: flex-start;
  background-color: #fff1a6;
}

.weather-alert {
  align-items: flex-end;
}

.modal-header {
  width: 96%;
  text-align: center;
  font-weight: bolder;
  font-size: 1.75em;
  color: white;
  background-color: red;
}

.modal-section {
  text-align: center;
  font-weight: bold;
}

.alert-modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
}

.alert-modal > div {
  background-color: #f1f1f1;
  width: 96%;
  margin: 1%;
  text-align: center;
  /* line-height: 75px; */
  font-size: 25px;
}

.main-temp {
  margin: 0;
  font-size: 5em;
  align-items: center;
}

.main-location {
  margin: 0;
  font-size: 2em;
}

.main-detailed {
  margin: 0;
  font-size: 1.5em;
}

.main-updated {
  margin-top: 2em;
  font-size: 1em;
}

.forecast-item {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid #ffffff;
  padding: 20px;
  transition: all 0.2s;
  /* width: auto; */
  /* background-color: #00FF9F */

  /* background-color: #F3872F; */
}

.forecast-item:hover {
  background-color: #00E58F;
  cursor: pointer;
}

.forecast-item h6 {
  color: #888;
  margin-top: 5px;
}

.forecast {
  text-align: right;
}

.weather-forecast {
  /* width: 50%; */
  display: flex;
  flex-wrap: wrap;
}

.weather-forecast > div {
  flex: 0 50%;
}

@media only screen and (max-width: 600px) {
    .body {
    flex-flow: column;
  }

  .modal-header {
    width: 100%;
    text-align: center;
    font-weight: bolder;
    font-size: 1.5em;
    color: white;
    background-color: red;
  }

  .alert-modal {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
  }
  
  .alert-modal > div {
    background-color: #f1f1f1;
    width: 100%;
    margin: 1%;
    text-align: center;
    /* line-height: 22px; */
    /* font-size: 16px; */
    font-size: 1em;
  }
}

.tabs {
  /**
   * Setting display to flex makes this container lay
   * out its children using flexbox, the exact same
   * as in the above "Stepper input" example.
   */
  display: flex;

  border-bottom: 1px solid #D7DBDD;
}

  .tab {
    cursor: pointer;
    padding: 5px 30px;
    color: #16A2D7;
    font-size: 12px;
    border-bottom: 2px solid transparent;
  }

  .tab.is-tab-selected {
    border-bottom-color: #4EBBE4;
  }
